@import "./src/assets/sass/prepends.scss";

.attachent-wrapper {
  margin: 2rem 0;
}
.attachent-wrapper label * {
  pointer-events: none;
}

.attachent-wrapper > label {
  display: inline-block;
  border: 1px solid $bg-dark;
  border-radius: 4px;
  background-color: $bg-white;
  padding: 1rem;
  transition: 0.5s;
}
.attachent-wrapper > label.attachment-opened {
  display: block;
  width: 100%;
  text-align: center;
  padding: 4rem;
}

#attachment-input {
  display: none;
}

.attachment-preview {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}
.attachment-preview-wrapper {
  position: relative;
}

.file-cansel {
  position: absolute;
  text-align: center;
  top: 0;
  right: 0;
  border: 1px solid #aaa;
  border-radius: 4px;
  margin: 0.5rem;
  width: 32px;
  height: 32px;
  line-height: 30px;
  font-size: 20px;
  background-color: rgba(255, 255, 255, 0.4);
  transition: 0.2s;
  z-index: 2;
  cursor: pointer;
}
.file-cansel:hover {
  background-color: rgba(255, 255, 255, 0.95);
}
