@import "./src/assets/sass/prepends.scss";

section.card {
  padding: 1rem;

  > div {
    margin: 0 0 1rem;
    > div {
      margin: 0 0 0.5rem;
    }
  }

  span.to-address {
    display: inline-block;
    background-color: $bg-light-lighten3;
    border-radius: 0.5rem;
    padding: 0.25rem;
    margin: 0 0.25rem 0.25rem 0;
    line-height: 1.5;
  }
  div.message-content{
    white-space: pre-wrap;
    line-height: 1.5;
    word-break: break-all;
  }

  div.attachment-wraper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
  }
}
