@import "./src/assets/sass/prepends.scss";

ul {
  list-style: none;
  margin: 5px 0 0 1rem;
  padding: 0;
  color: $text-red;

  li {
    background-color: $bg-white;
    display: inline-block;
    padding: 0.25rem;
    margin: 0 0 1px 0;
    border-radius: 4px;
    background-color: $bg-red-light;
  }
}
